class Prices extends React.Component {
  state = {
    currency: "USD"
  };

  render() {
    const { bpi } = this.props;
    let desc = bpi.USD.description;
    let code = bpi.USD.code;
    let rate = bpi.USD.rate;

    let list = "";

    if (this.state.currency === "USD") {
      desc = bpi.USD.description;
      code = bpi.USD.code;
      rate = bpi.USD.rate;
    } else if (this.state.currency === "GBP") {
      desc = bpi.GBP.description;
      code = bpi.GBP.code;
      rate = bpi.GBP.rate;
    } else if (this.state.currency === "EUR") {
      desc = bpi.EUR.description;
      code = bpi.EUR.code;
      rate = bpi.EUR.rate;
    }

    return (
      <div>
        <ul className="list-group">
          <li className="list-group-item">
            Bitcoin rate for {desc} :{" "}
            <span className="badge badge-primary">{code}</span>{" "}
            <strong>{rate}</strong>
          </li>
        </ul>
        <br />
        <select
          onChange={e => this.setState({ currency: e.target.value })}
          className="form-control"
        >
          <option value="USD">USD</option>
          <option value="GBP">GBP</option>
          <option value="EUR">EUR</option>
        </select>
      </div>
    );
  }
}

export default Prices;



